import store from '@/store/store'
import {formatDate} from '@/assets/js/tool'
import {subSpending,getSpending,login} from '@/assets/userapi'
  export default {
      name: "day",
    store,
    data () {
      return {
        form: {
          date: '',
          breakfast: 0,
          lunch: 0,
          dinner: 0,
          traffic: 0,
          sock: 0,
          work: 1,
          clothes: 0,
          clothesRemind: "",
          play: 0,
          playRemind: "",
          others: 0,
          othersRemind: "",
          user:""
        },
        now:"",
      }
    },
    mounted: function () {
      let date=new Date()
      this.now= this.form.date = formatDate(date, "yyyy-MM-dd")
      this.init()
      this.form.user=this.$store.state.name

    },
    computed:{
      eat(){
        return ''+(parseFloat(this.form.breakfast)+parseFloat(this.form.lunch)+parseFloat(this.form.dinner))+''
      },
      all(){
        return parseFloat(this.eat)+parseFloat(this.form.traffic)+parseFloat(this.form.sock)+parseFloat(this.form.clothes)+parseFloat(this.form.play)+parseFloat(this.form.others)
      }
    },
    methods:{
        gosubmit(){
          const _this = this //  onCancel  onConfirm  this 
          console.log("asdasdas")
          this.$vux.confirm.show({
            // show
            content:"",
            onCancel () {
              console.log(this) //  vm
              console.log(_this) //  vm
            },
            onConfirm () {
              _this.onSubmit()
            }
          })
        },
      showPlugin(){
        let that=this
        this.$vux.datetime.show({
          cancelText: '',
          confirmText: '',
          format: 'YYYY-MM-DD',
          value: this.form.date,
          onConfirm (val) {
            console.log(that.form.date)
            that.form.date=val
            if(that.form.date<'2018-05-29'){
              that.form.date='2018-05-29';
              that.$vux.toast.error('2018-05-29');

            }
            if(that.form.date>that.now){
              that.form.date=that.now;
              that.$vux.toast.error(that.now+'');

            }
            that.init()
          },
          onShow () {
            console.log('plugin show')
          },
          onHide () {
            console.log('plugin hide')
          }
        })
      },
      onSubmit() {
        var obj = this.form;
        let flag = 1;
        let error = ""
        for (let i in obj) {
          if (!(i == "playRemind" || i == "clothesRemind" || i == "othersRemind"||i == "date"||i=='user')) {
            console.log((obj[i]))
            if ((obj[i]+"")=="") {
              flag = 0;
              error = i+''
              break
            }
            obj[i]=parseFloat(obj[i])
            console.log((obj[i]))
          }
        }
        if(flag==0){
          this.$vux.toast.error(error)
          return
        }
        var that=this;
        console.log(obj)
        subSpending(obj).then(function (data) {
          data=data.data
          if(data.code==1){
            that.$vux.toast.show("")
            that.$emit('goinit')
          }else{
            console.log(error)
            that.$vux.toast.error("")
          }
        }).catch(function (error) {
          that.$vux.toast.error('');
          console.log(error);
        });

      },
      init(){
        var that=this
        getSpending(this.form.date).then(function (data) {
          console.log(data)
          data=data.data
          if(data.code==1){
            if(data.data.length>0){
              that.form=data.data[0]
            }else{
              for(let i in that.form){
                console.log(i)
                if (!(i == "playRemind" || i == "clothesRemind" || i == "othersRemind"||i == "date"||i=='user')) {
                  that.form[i]=0
                }else {
                  if(i != "date"){
                    that.form[i]=""
                  }
                }

              }
            }
          }else{
            that.$vux.toast.error('');
          }

        }).catch(function (error) {
          that.$vux.toast.error('');
        })
      },
      formatInput(val){

      },
      blurInput(val){
        console.log(val)
        let that=this;
        let num=this.form[val]+"";
        console.log(num)
        num=num.replace(/[^\-?\d.]/g,'');
        if(num==""){
          num=0
        }
        this.$set(that.form,val,num)

      }
    }
  }



