import store from '@/store/store'
import {getSumByDate,changePassword} from '@/assets/userapi'

  export default {
      name: "month",
    store,
    data:function () {
      return {
        month:"",
        count:0,
        now:"",
        form: {
          breakfast: 0,
          lunch: 0,
          dinner: 0,
          traffic: 0,
          sock: 0,
          clothes: 0,
          play: 0,
          others: 0,
        },
      }
    },
    mounted:function(){
      let date=new Date();
      let month=date.getMonth()+1
      let year=date.getFullYear()
      month=(month <10)?("0"+month):month
      this.month=year+'-'+month
      console.log(this.nextmonth)
      this.now=year+'-'+month;
      this.init();

    },
    computed: {
      name() {
        return this.$store.state.name
      },
      eat(){
        return (this.form.breakfast+this.form.lunch+this.form.dinner).toFixed(2)
      },
      nextmonth(){
        let arr=this.month.split("-")
        let time=""
        if(parseInt(arr[1])==12){
          time= (parseInt(arr[0])+1)+'-01'
        }else {
          let m=(parseInt(arr[1])+1);
          time= arr[0]+'-'+((m<10)?('0'+m):m)
        }
        return time
      }
    },
    methods: {
      showPlugin(){
        let that=this
        this.$vux.datetime.show({
          cancelText: '',
          confirmText: '',
          format: 'YYYY-MM',
          value: this.month,
          onConfirm (val) {
            that.month=val
            console.log(that.nextmonth)
            if(that.month<'2018-05'){
              that.month='2018-05';
              that.$vux.toast.error('2018-05');

            }
            if(that.month>that.now){
              that.month=that.now;
              that.$vux.toast.error(that.now+'');

            }
            that.init()
          },
          onShow () {
            console.log('plugin show')
          },
          onHide () {
            console.log('plugin hide')
          }
        })
      },
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      init(){
        let that=this;
        that.count=0;
        getSumByDate(this.month,this.nextmonth).then(function (data) {
          console.log(data.data.data[0])
          let obj=data.data.data[0]
          let newObj={}
          for(let i in obj){
            if(obj[i]==null){
              that.count+=0;
              let str=i.split(')')[0].split('(')[1]
              that.form[str]=0
            }else{
              that.count+=obj[i];
              console.log(obj[i])
              let str=i.split(')')[0].split('(')[1]

              that.form[str]=obj[i]
            }

          }
        }).catch(function (data) {
          console.log(data)
        })
      },
      change(){

      },
      loginout(){
        this.$confirm('?', '', {
          cancelButtonText: '',
          confirmButtonText: '',
          type: 'warning'
        }).then(() => {
          this.out();
        }).catch(() => {

        });
      },
      out(){
        sessionStorage.removeItem('name');
        this.$router.push('/')
      },
      changePas(){
        if(this.passsword.origain==''){
          this.$vux.toast.error('');
          return
        }
        if(this.passsword.newword==''){
          this.$vux.toast.error('');
          return
        }
        if(this.passsword.remewword==''){
          this.$vux.toast.error('');
          return
        }
        if(this.passsword.remewword!=this.passsword.newword){
          this.$vux.toast.error('');
          return
        }
        let that=this;
        changePassword(this.passsword.origain,this.passsword.newword).then(function (data) {
          console.log(data)
          let  newdata=data.data;
          if(newdata.code==1){
            that.$vux.toast.show('');
            that.dialogVisible=false
          }else{
            that.$vux.toast.error(newdata.msg);
          }
        }).catch(function (error) {
          console.log(error)
          that.$vux.toast.error('');

        })

      },
      getTwo(val){
        return parseInt(val*100)/100.0
      }

    }
  }



